.infoList > li {
  display: inline-block;
  width: 100%;
  padding: 6px 0;
}
.infoList > li .item {
  display: flex;
  white-space: nowrap;
}
.infoList > li .item .label {
  color: #AEAEAE;
}
.infoList > li .item .label:after {
  content: ':';
  margin-right: 4px;
}
.infoList > li .item .value {
  flex: auto;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
